<template>
    <div id="cases" ref="cases" class="scroll-wrapper cases case-animation">
       
        <item v-for="(i,idx) in schema" :i="i" :id="`case${idx}`" :key="idx" />
        <!--internal links need for SEO-->
        <a :href="`/case/${i.id}`" v-for="(i,idx) in schema" style="display: none;">{{i.name}}</a>

        <organization-schema-org />
    </div>
</template>

<script>
    import schema from '../modules/schema'
    import item from './modules/item.vue'

    export default {
        metaInfo: {
            meta: [
                {
                    vmid: 'description',
                    name: 'description',
                    content: 'Online studio specialising in Nodejs. Integration TradingView.com (charting_library) and TradingVue.js charts. Developing trading bots and algorithms.'
                },
            ],
        },
        data: () => ({
            schema
        }),
        mounted() {
            if (window.innerWidth >= 600) this.scroll(this.$refs.cases)
        },
        methods: {
            scroll(el) {
                el.addEventListener('mousewheel', (e) => {
                    e.preventDefault() // disable scroll

                    let wDelta = Math.sign(e.wheelDelta)
                    el.scrollLeft -= 120 * wDelta

                    return false
                }, false)
            }
        },
        components: {
            item
        }
    }
</script>