<template>
    <preview :i="i" :id="id" @click="onClick" />
</template>

<script>
    // https://github.com/duskload/mobile-device-detect
    import { isMobile } from 'mobile-device-detect'

    export default {
        props: ['i', 'id'],
        data: () => ({
            isOpen: false,
            refs: {}
        }),
        mounted() {
            this.refs = {
                container: document.querySelector(`#${this.id}`),
                details: document.querySelector(`#${this.id} .case-details`),
                name: document.querySelector(`#${this.id} .case-details__name`),
                subname: document.querySelector(`#${this.id} .case-details__subname`),
            }
        },
        methods: {
            onRouteBack() {
                this.onClick(0)
            },
            onClick(duration = 800) {
                this.refs.container.classList.toggle('js-no-hover')

                let options;

                if (isMobile) {
                    options = {
                        height: {
                            value: this.isOpen ? '50%' : '100%',
                            duration,
                        },
                    }
                } else {
                    options = {
                        width: {
                            value: this.isOpen ? '33.33%' : '100%',
                            duration,
                        },
                    }
                }

                const scrollOptions = isMobile ? { scrollTop: this.refs.container.offsetTop } : { scrollLeft: this.refs.container.offsetLeft }

                // scroll
                this.$anime({
                    targets: document.querySelector('#cases'),
                    easing: 'easeInOutCirc',
                    duration,
                    ...scrollOptions
                })

                this.$anime({
                    targets: this.refs.container,
                    easing: 'easeInOutCirc',
                    duration,
                    begin: () => {
                        this.isOpen = !this.isOpen
                    },
                    complete: () => {
                        // route to case page
                        this.$router.push({ name: 'case', params: { id: this.i.id } })
                    },
                    ...options,
                })

            },
        }
    }
</script>